*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container {
    background-color: aliceblue;
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.app-wrapper {
    background-color: pink;
    min-width: 450px;
    min-height: 700px;
    padding: 30px;
    margin-bottom: 10px;
    box-shadow: 5px 5px 5px rgb(100, 100, 100);
    border-radius: 10px;
}
.header h1{
    color: black;
    text-align: center;
    margin: 20px 0;
    font-size: 50px;
}
.task-input{
    outline: none;
    width: 300px;
    padding: 10px;
    margin-right: 20px;
    font-size: 20px;
    border-radius: 10px;
}
.button-add {
    width: 70px;
    padding: 10px;
    font-size: 20px;
    border-radius: 10px;
    border: 2px solid black;
    background-color: pink;
    cursor: pointer;
    color: black;
}
.button-add:hover{
    background-color: rgb(221, 130, 145);
    transition: 0.7s;
}
.list-item{
    display: flex;
    margin: 20px 0;
    border: 2px solid black;
    border-radius: 10px;
    max-height: 50px;
}
.button-complete, .button-delete, .button-edit{
    border: none;
    outline: none;
    font-size: 25px;
    background-color: transparent;
    cursor: pointer;
    color: black;
    margin-right: 15px;
}
.complete {
    text-decoration-style: solid;
    text-decoration-line: line-through;
}
.list {
    width: 250px;
    background-color: transparent;
    font-size: 20px;
    border-radius: 10px;
    padding-left: 10px;
    border: none;
    margin-right: 20px;
}
input:focus, button:focus{
    outline: none;
}